@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
.placeholder {
  border-radius: 5px;
  background-color: #f2f2f2; }
  .placeholder.h5 {
    height: 32px;
    width: 160px; }
  .placeholder.subtitle2 {
    height: 24px;
    width: 200px; }
  .placeholder.subtitle1 {
    height: 24px;
    width: 80%; }
  @media only screen and (min-width: 960px) {
    .placeholder.h3 {
      height: 50px;
      width: 150px; }
    .placeholder.h6 {
      height: 28px;
      width: 60%; }
    .placeholder.list-item-text {
      height: 24px;
      width: 30%; } }
  @media only screen and (max-width: 959px) {
    .placeholder.h3 {
      height: 50px;
      width: 80%; }
    .placeholder.h6 {
      height: 56px;
      width: 100%; }
      .placeholder.h6.multi-line-xs {
        height: 112px;
        width: 80%; }
    .placeholder.subtitle1.multi-line-xs {
      width: 100%;
      height: 192px; }
    .placeholder.list-item-text {
      height: 24px;
      width: 80%; } }

.placeholder-icon {
  color: #f2f2f2; }

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

